import { createApp } from 'vue'
import App from '@/App.vue'
import router from './router'
import axios from 'redaxios'

const dataPath = 'data/data.json'

const loadApp = async () => {
  const { data: items } = await axios.get(dataPath)

  createApp(App)
    .use(router)
    .provide('items', items)
    .mount('#app')
}

loadApp()
