<template>
  <span />
</template>

<script language="ts">
import { onMounted, defineComponent } from 'vue'
import patchMeta from './patch_meta'

export default defineComponent({
  props: {
    siteName: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    description: {
      type: String,
      required: true
    },
    previewUrl: {
      type: String,
      default: location.href
    },
    shareUrl: {
      type: String,
      default: location.href
    }
  },
  setup (props) {
    onMounted(() => {
      patchMeta(props)
    })
  }
})
</script>
