
import { defineComponent, inject } from 'vue'
import CustomNavBar from '@shared/bootstrap-components/CustomNavBar.vue'
import CustomFooter from '@shared/bootstrap-components/CustomFooter.vue'
import PatchMeta from '@shared/vue-utils/PatchMeta.vue'
import ImageCarousel from '@shared/bootstrap-components/ImageCarousel.vue'
import { ShopItem } from '@shared/types/ShopItem'
import 'bootstrap/dist/css/bootstrap.css'

export default defineComponent({
  components: {
    CustomNavBar,
    CustomFooter,
    ImageCarousel,
    PatchMeta
  },
  setup () {
    const data: Record<string, ShopItem> = inject('items', {})

    const allBrands = Object.values(data)
      .filter((i) => i.brand)
      .map((i) => i.brand.trim())

    const counts: Record<string, number> = {}
    allBrands.forEach((b) => {
      counts[b] = counts[b] ? counts[b] + 1 : 1
    })
    const sections: Record<string, string> = {}
    allBrands.forEach((b) => {
      sections[`${b} (${counts[b]})`] = `/${b}`
    })

    return {
      sections
    }
  }
})
